@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Poppins:wght@100&display=swap);
form {
  display: grid;
  grid-template-columns: auto auto;
  width: 100%;
}
.Input_input__1NoCh {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid white;
  outline: none;
  font-family: "open sans";
  color: white;
  font-weight: bold;
  width: 100%;
  background: transparent;
  transition: border-radius 250ms;
}
.Input_input__1NoCh:focus {
  border-radius: 2rem;
}
.Input_mainDiv__3qbAs {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Input_buttons__1uN5C {
  display: grid;
  grid-template-columns: auto auto;
}

button{
  padding: 10px;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-left: 5px;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  background: linear-gradient(
    to right,
    #5500FF,
    #220066
  );
  color: #fff;
  transition: border-radius 250ms;
}
button:hover {
  border-radius: 2rem;
}
@media screen and (max-width: 900px) {
  .Input_mainDiv__3qbAs form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .Input_input__1NoCh {
    width: 75%;
  }
}

.TodoDivs_div__2ANzE{
  font-family: Poppins;
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
}
.TodoDivs_div__2ANzE p {
  width: 100%;
  font-weight: bold;
}
.TodoDivs_div__2ANzE p span {
  color: rgb(0, 255, 0);
  font-size: 15px;
}
.TodoDivs_strikethrough__1yEhT {
  text-decoration: line-through;
}
.TodoDivs_nothingHere__1Tpa_ {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgba(150, 150, 150, 0.5);
}
@media screen and (max-width: 900px) {
  .TodoDivs_div__2ANzE {
    flex-direction: column;
    align-items: center
  }
  .TodoDivs_div__2ANzE button {
    width: 50vw;
  }
  .TodoDivs_nothingHere__1Tpa_ h2 {
    font-size: 15px;
  }
}

.Alert_alert__3fdS3 {
  width: 100vw;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 2rem;
  color: rgba(255, 255, 255, 0.5);
  font-family: Poppins;
  text-align: center;
}
.Alert_alert__3fdS3 p {
  padding-top: 5px;
  font-size: 23px;
}
.Alert_alert__3fdS3 p span {
  font-weight: bolder;
}

*::before,
*,
*::after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}
body {
  background: rgb(48, 16, 255)
}

h1,
h2,
h3,
p {
  font-family: "open sans";
}

.parent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border-radius: 2rem;
  color: white;
  width: 60vw;
}
.parent h3 {
  font-family: Poppins;
  font-weight: bolder;
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center;
}
.innerParent {
  margin: 50px;
  max-height: 75vh;
  overflow-y: scroll;
}
.todos{
  display: block;
}

@media screen and (max-width: 900px) {
  .parent{
    display: block;
    width: 100vw;
    height: 100vh;
    border-radius: 0px;
  }
  .innerParent {
    margin: 2rem 5px 5px 0px;
  }
  .todos {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}

